import gsap from 'gsap'

import ArticleIntro from '@/components/Blocks/ArticleIntro'
import ArticleTextBlock from '@/components/Blocks/ArticleTextBlock'
import ArticleVideo from '@/components/Blocks/ArticleVideo'
import ArticleBigImage from '@/components/Blocks/ArticleBigImage'
import ArticleBigNav from '@/components/Blocks/ArticleBigNav'
import ArticleCard from '@/components/Card/ArticleCard'
import ArticlePhotoWall from '@/components/Blocks/ArticlePhotoWall'

import functions from '@/compositions/functions'
const { loadImage } = functions()

export default {
    components: {
        ArticleIntro,
        ArticleTextBlock,
        ArticleVideo,
        ArticleBigImage,
        ArticleBigNav,
        ArticleCard,
        ArticlePhotoWall,
    },
    async asyncData ({ $config, route, $axios, error }) {
        try {
            const res = await $axios.$get(`${$config.APP_BACKEND_API}/wp-json/api/single_projects/${route.params.slug}`)
            if (res.error === 'Page Not Found') {
                error({ statusCode: 404, message: 'Project not found' })
                return
            }

            const data = res.data
            const breadcrumbs = data.breadcrumbs.slice(0, -1)

            const seo = data.fields.seo

            return {
                data,
                breadcrumbs,
                seo,
            }
        } catch (e) {
            console.log(e)
            error({ statusCode: 404, message: 'Post not found' })
            return {
                data: {},
                breadcrumbs: {},
                seo: {},
            }
        }
    },
    data () {
        return {
            data: null,
            breadcrumbs: [],
        }
    },
    head () {
        return {
            title: this.seo.title + ' | ' + this.$config.APP_TITLE_TEMPLATE,
            meta: [
                { hid: 'description', name: 'description', content: this.seo.desc },
                { hid: 'og:title', property: 'og:title', content: this.seo.title },
                { hid: 'og:description', property: 'og:description', content: this.seo.desc },
                { hid: 'og:url', property: 'og:url', content: `${process.env.APP_URL}${this.$route.fullPath}` },
                { hid: 'og:site_name', property: 'og:site_name', content: this.seo.title },
                { hid: 'og:image', property: 'og:image', content: this.seo.thumb ? this.seo.thumb : `${process.env.APP_URL}/og_img.jpg` },
                { hid: 'og:image:alt', property: 'og:image:alt', content: this.seo.desc },
                { hid: 'twitter:description', name: 'twitter:description', content: this.seo.desc },
                { hid: 'twitter:title', name: 'twitter:title', content: this.seo.title },
                { hid: 'twitter:image', name: 'twitter:image', content: this.seo.thumb ? this.seo.thumb : `${process.env.APP_URL}/og_img.jpg` },
            ],
        }
    },
    mounted () {
        this.$store.dispatch('ADD_LOADING_STACK', loadImage())
        this.$store.dispatch('WAIT_LOADING')

        this.gsapInit()
    },
    methods: {
        getComponent (val) {
            switch (val) {
                case 'text_block':
                    return 'ArticleTextBlock'
                case 'video':
                    return 'ArticleVideo'
                case 'big_image':
                    return 'ArticleBigImage'
                case 'photo_wall':
                    return 'ArticlePhotoWall'
                default:
                    return ''
            }
        },
        gsapInit () {
            gsap.fromTo('.article-intro__content', { x: -20, opacity: 0 }, { x: 0, opacity: 1 }).delay(1)
            gsap.fromTo('.article-intro__image', { opacity: 0 }, { opacity: 1 }).delay(1)
            gsap.fromTo('.projects-content > .container > div:nth-child(2)', { top: 30, opacity: 0 }, { top: 0, opacity: 1 }).delay(1.3)
            gsap.utils.toArray('.projects-content > .container > div').slice(2).forEach((el) => {
                const tl = gsap.timeline({
                    scrollTrigger: {
                        trigger: el,
                        start: 'top bottom-=25%',
                        toggleActions: 'play none none reverse',
                    },
                })
                tl.fromTo(el, { top: 30, opacity: 0 }, { top: 0, opacity: 1 })
            })
            const tl = gsap.timeline({
                scrollTrigger: {
                    trigger: '.projects-content__recommend > .container',
                    start: 'top bottom-=25%',
                    toggleActions: 'play none none reverse',
                },
            })
            tl
                .fromTo('.projects-content__recommend__title', { y: 30, opacity: 0 }, { y: 0, opacity: 1 })
                .fromTo('.projects-content__recommend__articles-item', { y: 30, opacity: 0 }, { y: 0, opacity: 1, stagger: 0.2 }, '-=0.2')
        },
    },
}
